/* $$$$$$$$$ RESET STYLES $$$$$$$$$$ */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* Nastavení výchozího box modelu */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Nastavení výchozího pozadí a fontu */
body {
  line-height: 1;
  background-color: #fff;
  color: #000;
  font-family: Arial, sans-serif;
}

ol, ul {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}



/* $$$$$$$$$ GLOBAL STYLES $$$$$$$$$$ */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #000000;
  --secondary-color: #f8f8f8f6;
  --text-color: #1f1f1f;
  --text-color-light: #555;
  --background-color: #fff;
  --button-color: #1b1b1b;
  --button-hover-color: #626262;
}


/* $$$$$$$$$$ HEADER $$$$$$$$$$ */

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #f8f8f8;
}

.nav-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.nav-left .logo {
  width: 40px;
  height: 40px;
  margin-top: 10px;
}

.nav-left span {
  font-size: 1.2rem;
  font-weight: bold;
  color: #333;
}

.nav-right {
  display: block;
}

.nav-right ol {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.nav-right ol li a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
  transition: color 0.3s ease;
  text-transform: uppercase;
}

.nav-right ol li a:hover {
  color: var(--text-color-light);
}

.nav-right li {
  position: relative;
}

.dropbtn {
  background: none;
  border: none;
  color: var(--text-color);
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  text-transform: uppercase;
  transition: color 0.3s ease;
}

.dropbtn:hover {
  color: var(--text-color-light);
}

.dropdown-content {
  position: absolute;
  top: 100%;
  left: -35%;
  background-color: var(--background-color);
  box-shadow: 0px 8px 16px rgba(0,0,0,0.2);
  z-index: 1;
  min-width: 120px;
}

.dropdown-content a {
  display: block;
  padding: 10px 15px;
  color: var(--text-color);
  text-align: center;
  text-decoration: none;
  font-weight: normal;
  text-transform: none;
}

.dropdown-content a:hover {
  background-color: #ddd;
  color: var(--text-color-light);
}



/* $$$$$$$$$ LENA SECTION $$$$$$$$$$ */
.lena-section {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: #f9f9f9;
}

.lena-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  align-items: center;
  text-align: left;
}

.lena-left .lena-image {
  width: 100%;
  max-width: 300px;
  margin-top: 10px;
}

.lena-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  margin-left: 20px;
}

.button-booking {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  padding: 10px 20px;
  background-color: var(--button-color);
  color: #fff;
  border: none;
  border-radius: 60px;
  cursor: pointer;
  text-decoration: none;
  font-family: "Pacifico", cursive;
  font-weight: 400;
  font-style: normal;
  font-size: 2rem;
  transition: background-color 0.3s ease;
}

.button-booking:hover {
  background-color: var(--button-hover-color);
}

.lena-right h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  font-family: "Lora", serif;
}

.lena-right p {
  font-size: 1rem;
  line-height: 1.6;
  color: #555;
  font-family: "Lato", sans-serif;
}

